class app{
    constructor(){
        this._getPosition();
        searchIcon.addEventListener('click',this._searchCity.bind(this))
    }
      _getPosition(){
        navigator.geolocation.getCurrentPosition(this._reverseGEO.bind(this),function(){
            alert('Could not get your location')
        },{
            enableHighAccuracy:true
        })
        
    }
     async _weatherAPI(city){
        try{
            const res = await fetch(`http://api.weatherapi.com/v1/current.json?key=a607c130ae0447dabcc233412220501&q=${city}&aqi=no
            `);
            if(!res.ok) throw new Error(`Can't access location, try again`)
            const data = await res.json();

            return data;
        }catch(err){
            this._errorHandle(err)
        }
    }

     async _reverseGEO(pos){
        try{
            const {longitude,latitude} = pos.coords;
            // if(!pos) return
            const resp =await fetch(`https://geocode.xyz/${latitude},${longitude}?json=1`);
            if(!resp.ok) throw new Error('Could not get location')
       const data = await resp.json();
       const {city} = data;
        const weatherData = await this._weatherAPI(city);
        console.log(weatherData)
        this._markup(weatherData)
        }catch(err){
            this._errorHandle(err)
        }
       
    
    }
    _markup(data){
        if(!data) return
        const markup = `
            
                <div class="weather__data">
                  <h3 class="weather__name">Weather in ${data.location.name}</h3>
                  <h4 class="weather__temp">${data.current.temp_c}°C</h4>
                  <p class="weather__row">${data.current.condition.text}</p>
                  <p class="weather__row">Humidity: ${data.current.humidity}%</p>
                  <p class="weather__row">Wind: ${data.current.wind_kph}km/h</p>
                </div>
              
            `
weatherContainer.insertAdjacentHTML('beforeend',markup)

    }
    async _searchCity(){
        try{
            const city=searchInput.value.split(' ').map(el => el[0].toUpperCase() + el.slice(1)).join(' ');
            const queries = [document.querySelector('.weather__data'),document.querySelector('.error__msg')];
            queries.forEach(el => el?.remove())   
            const data = await this._weatherAPI(city);
    
            this._markup(data);
        }catch(err){
            console.error(err)
        }
        
    }
    _errorHandle(err){
        const markup = `<p class="error__msg">${err}</p>`
        weatherContainer.insertAdjacentHTML('beforeend',markup)
    }
    
    
}
const ap1 = new app()